import React, { useContext } from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { fade, makeStyles } from '@material-ui/core/styles';
import { useScrollTrigger } from '@material-ui/core';
import Brightness7Icon from '@material-ui/icons/Brightness7';
import Brightness4Icon from '@material-ui/icons/Brightness4';
import { mainContext } from '../main-context';
import grey from '@material-ui/core/colors/grey';
import Logo from '../images/Logo3.png';
const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    background: grey[800],
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  search: {
    'position': 'relative',
    'borderRadius': theme.shape.borderRadius,
    'backgroundColor': fade(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    'marginLeft': 0,
    'width': '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(1),
      width: 'auto',
    },
  },
  searchIcon: {
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      'width': '12ch',
      '&:focus': {
        width: '20ch',
      },
    },
  },
  logo: {
    width: 100,
  },
}));

export default function SearchAppBar(props) {
  const classes = useStyles();
  const main = useContext(mainContext);
  const trigger = useScrollTrigger({
    disableHysteresis: true,
    threshold: 100,
  });

  const handleClick = () => {
    main.update({ ...main, drawer: true });
  };

  const handleDark = () => {
    main.update({ ...main, dark: !main.dark });
  };
  return (
    <div id='top' className={classes.root}>
      <AppBar
        style={{ transition: 'all 0.5s ease-out' }}
        position='fixed'
        className={!trigger ? 'MuiAppBar-colorTransparent' : classes.root}
        elevation={!trigger ? 0 : 2}>
        <Toolbar>
          {/* <IconButton
            edge='start'
            className={classes.menuButton}
            onClick={handleClick}
            color='secondary'
            aria-label='open drawer'>
            <MenuIcon />
          </IconButton> */}
          <img className={classes.logo} src={Logo} alt='Logo' />
          <Typography
            className={classes.title}
            variant='h6'
            noWrap></Typography>
          <IconButton
            edge='start'
            className={classes.menuButton}
            onClick={handleDark}
            color='primary'
            aria-label='open drawer'>
            {main.dark ? <Brightness7Icon /> : <Brightness4Icon />}
          </IconButton>
        </Toolbar>
      </AppBar>
    </div>
  );
}
